import React from 'react';
import './home.scss'
import ApartmentIcon from '@mui/icons-material/Apartment';
import DirectionsCarIcon from '@mui/icons-material/DirectionsCar';
import KeyboardDoubleArrowDownIcon from '@mui/icons-material/KeyboardDoubleArrowDown';

const Home = React.forwardRef((props, ref) => {
  return (
    <section  ref={ref}  id="home" >
      <div className="container">
        <div class="offer top_left">
          <div style={{display: 'flex', justifyContent:'space-around', alignItems: 'center'}}>
          
            <h2>Uslugi Leasingowe </h2>
            <DirectionsCarIcon style={{fontSize: '2rem', color: '#ceac65'}}></DirectionsCarIcon>
            </div>
            <p>Dostarczamy najlepsze na rynku rozwiązania finansowe dopasowane do Twoich potrzeb.
            Na pierwszym miejscu stawiamy uczciwą i partnerską współpracę, dzięki której budujemy trwałe relacje oparte na zaufaniu i profesjonalizmie. Nasi eksperci zapewniają kompleksowe wsparcie na każdym etapie sprawy od wyszukania pojazdu po obsługę aktywnej umowy.</p>
            <div onClick={props.scrollOffers} class="offer-btn">
              <span >Oferta</span>
              <KeyboardDoubleArrowDownIcon></KeyboardDoubleArrowDownIcon>
            </div>
        </div>
        <div class="offer bottom_right">
        <div style={{display: 'flex', justifyContent:'space-around', alignItems: 'center'}}>
            <h2>Uslugi Kredytowe</h2>
            <ApartmentIcon style={{fontSize: '2rem', color: '#ceac65'}}></ApartmentIcon>
            </div>
            <p>Dostarczamy najlepsze na rynku rozwiązania finansowe dopasowane do Twoich potrzeb.
            Na pierwszym miejscu stawiamy uczciwą i partnerską współpracę, dzięki której budujemy trwałe relacje oparte na zaufaniu i profesjonalizmie. Nasi eksperci zapewniają kompleksowe wsparcie na każdym etapie sprawy od wyszukania pojazdu po obsługę aktywnej umowy.</p>
            <div onClick={props.scrollOffers} class="offer-btn">
            <span >Oferta</span>
              <KeyboardDoubleArrowDownIcon></KeyboardDoubleArrowDownIcon>
            </div>
        </div>
      </div>
    </section>
  );
})

export default Home;
