import React from 'react';
import './about.scss';

const About = React.forwardRef((props, ref) => {
  return (
    <section  ref={ref} id="about" className="about">
      <div className="logo-container">
        <div className='logo'></div>
      </div>
      <div className="description">
        <h2>O Nas</h2>
        <p>
          Witamy w Leasing i Kredyty! Jesteśmy wiodącą firmą oferującą 
          innowacyjne rozwiązania finansowe dla naszych klientów w całej Polsce. 
          Nasz zespół ekspertów jest gotowy, aby pomóc Ci w znalezieniu najlepszego 
          rozwiązania leasingowego i kredytowego, dostosowanego do Twoich potrzeb.
        </p>
        <p>
          Nasza misja to zapewnienie najwyższej jakości usług i wsparcia, aby 
          Twoje finansowe decyzje były proste i korzystne. Dzięki wieloletniemu 
          doświadczeniu i szerokiej wiedzy na temat rynku, gwarantujemy, że jesteś 
          w dobrych rękach.
        </p>
      </div>
    </section>
  );
})

export default About;
