import React, { useEffect, useState } from 'react';
import './header.scss';

function Header({scrollHome,scrollAbout,scrollOffer,scrollFooter,scrollCalculator}) {
  const [isScrolled, setIsScrolled] = useState(false);

  useEffect(() => {
    const handleScroll = () => {
      const scrollTop = window.scrollY;
      setIsScrolled(scrollTop > 0); // Check if scrolled beyond the top
    };

    window.addEventListener('scroll', handleScroll);

    return () => {
      window.removeEventListener('scroll', handleScroll);
    };
  }, []);


  return (
    <header id='header' className={isScrolled ? 'scrolled' : ''}>
      
        <ul className='menu'>
          <li><a  onClick={scrollAbout}>O Nas</a></li>
          <li><a  onClick={scrollOffer}>Usługi</a></li>
          <li><a onClick={scrollHome}> <div className='logo'></div></a></li>
          <li><a  onClick={scrollCalculator}>Kalkulator</a></li>
          <li><a  onClick={scrollFooter}>Kontakt</a></li>
        </ul>
    </header>
  );
}

export default Header;
