import React from 'react';
import './ourOffer.scss';

const OurOffer = React.forwardRef((props, ref) => {
  return (
    <section  ref={ref} id="ourOffer">
        <div className='offerHeader'>
        <h2>Nasza oferta</h2>
        <p>Oferujemy elastyczne rozwiązania finansowe – od leasingu nowych aut osobowych po finansowanie specjalistycznych samochodów używanych. Skorzystaj z Naszego doświadczenia i wsparcia, aby łatwo zrealizować swoje plany zakupowe lub inwestycyjne. Dedykowany opiekun pomoże Ci wybrać najlepszy produkt finansowy, zaproponuje pojazd ze sprawdzonego źródła, a także zweryfikuje najważniejsze dokumenty. Dla Twojej wygody cały proces odbywa się w 100% on-line!</p>
        </div>
      <div className='offersContainer'>
      <div className='offerTile operation'><div className='tileTitle'><span>Leasing operacyjny</span></div></div>
      <div className='offerTile consumption'><div className='tileTitle'><span>Leasing konsumencki</span></div></div>
      <div className='offerTile bikkrd'><div className='tileTitle'><span>Leasing bez BIK i KRD</span></div></div>
      <div className='offerTile mortgage'><div className='tileTitle'><span>Kredyt Hipoteczny</span></div></div>
      <div className='offerTile financial'><div className='tileTitle'><span>Kredyt Finansowy</span></div></div>
      <div className='offerTile investment'><div className='tileTitle'><span>Kredyt inwestycyjny</span></div></div>
      </div>
    </section>
  );
})

export default OurOffer;
